import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-icon';
import styles from './styles.scss';
import {getStateData} from './getStateData';

const view = state => {
	const {type, widget, variant, color} = state.properties;
	const {icon, headerText, bodyText} = getStateData(type, widget);

	const sizeVariant = `empty-state empty-state-${variant.toLowerCase()}`;

	if (type === 'empty-announcement') {
		return (
			<div className="sn-hr-empty-state-announcement">
				<div className="empty-state-row">
					{icon}
				</div>
				<h3 className="empty-state-row">
					<span>{headerText}</span>
				</h3>
				<div className="empty-state-row">
					<span>{bodyText}</span>
				</div>
			</div>
		);
	}

	return (
		<div className={`${sizeVariant} sn-hr-empty-state`}>
			{icon}
			<p className="no-content-header" style={{color}}>{headerText}</p>
			<span className="no-content-body" style={{color}}>{bodyText}</span>
		</div>
	);
};

const TAG_NAME = 'sn-hr-empty-state';
createCustomElement(TAG_NAME, {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		type: {
			default: 'empty',
			schema: {
				type: 'string',
				enum: [
					'empty',
					'empty-announcement',
					'loading',
					'loading-error',
					'permission-error',
					'not-supported',
					'not-configured',
					'no-resources'
				]
			}
		},
		variant: {
			default: 'fit',
			schema: {
				type: 'string',
				enum: [
					'fit',
					'small',
					'medium',
					'large',
					'auto'
				]
			}
		},
		color: {schema: {type: 'string'}, default: '#637274'},
		widget: {schema: {type: 'string'}, default: ''}
	},
	styles
});

export default TAG_NAME;
