import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import styles from './emptyState.scss';
import defaultIcon from '../svgs/sn-hr-no-results.svg';

const view = state => {
	const {
		headerText,
		bodyText,
		icon
	} = state.properties;

	return (
		<div className="sn-empty-state">
			<div className="sn-empty-state-row">
				<img src={icon} alt={headerText}/>
			</div>
			<h3 className="sn-empty-state-row -header">
				<span>{headerText}</span>
			</h3>
			<div className="sn-empty-state-row">
				<span>{bodyText}</span>
			</div>
		</div>
	);
};

createCustomElement('sn-component-hr-empty-state', {
	renderer: {
		type: snabbdom,
		view
	},

	properties: {
		headerText: {
			schema: {type: 'string'},
			default: ''
		},
		bodyText: {
			schema: {type: 'string'},
			default: ''
		},
		icon: {
			schema: {type: 'string'},
			default: defaultIcon
		}
	},

	styles
});
