import '@servicenow/now-icon';
import '@servicenow/now-loader';
import emptyStateIcon from '../svgs/emptyStateIcon.svg';
import notSupportedIcon from '../svgs/notSupportedIcon.svg';
import announcementIcon from '../svgs/announcements-empty-state.svg';
import {t} from 'sn-translate';

export const getStateData = (type, widget) => {
	let icon = null;
	let headerText = '';
	let bodyText = t(
		'Contact your ServiceNow administrator for more information.'
	);

	if (type === 'loading') {
		icon = <now-loader />;
		headerText = t('Loading');
		bodyText = t('Loading{0} Data', widget ? ` ${widget}` : '');
	} else if (type === 'empty-announcement') {
		headerText = t('No announcements available.');
		bodyText = t('Have a great day!');
		icon = (
			<img
				src={announcementIcon}
				className="empty-state-icon"
				alt={headerText}
			/>
		);
	} else if (type === 'loading-error') {
		icon = (
			<now-icon
				className="empty-state-icon alert-critical"
				icon="exclamationTriangleOutline"
				size="xl"
			/>
		);
		headerText = t('This content could not be loaded.');
	} else if (type === 'permission-error') {
		icon = (
			<now-icon
				className="empty-state-icon alert-high"
				icon="exclamationTriangleOutline"
				size="xl"
			/>
		);
		headerText = t('You do not have permission to view this data.');
	} else if (type === 'not-supported') {
		icon = (
			<img
				src={notSupportedIcon}
				className="empty-state-icon"
				alt={t('Empty Container')}
			/>
		);
		headerText = t('Your Workspace does not support this visualization.');
	} else if (type === 'not-configured') {
		icon = (
			<now-icon
				className="empty-state-icon alert-critical"
				icon="exclamationTriangleOutline"
				size="xl"
			/>
		);
		headerText = t('This has not been setup.');
	} else if (type === 'no-resources') {
		icon = (
			<now-icon
				className="empty-state-icon neutral"
				icon="circle-close-outline"
				size="lg"
			/>
		);
		headerText = t('No resources available.');
		bodyText = t('Contact your ServiceNow administrator to set these up.');
	} else {
		icon = (
			<img
				src={emptyStateIcon}
				className="empty-state-icon"
				alt={t('Empty Container')}
			/>
		);
		headerText = widget ? t('No {0} content yet', widget) : t('No content available.');
		bodyText = '';
	}

	return {
		icon,
		headerText,
		bodyText
	};
};
